@import url("https://use.typekit.net/jmk3xov.css");

html {
  scroll-behavior: smooth;
 }

canvas {
  position: fixed;
  top: 0;
  left: 0;
}


:root {
  --dark-bg: rgba(15, 15, 15, 0.95);
  --white-bg: rgba(126, 130, 111, 0.1);

  --spacing: 350px;

  font-family: brandon-grotesque, sans-serif;
  font-weight: 400;
  font-style: normal;
}


main {
  width: 100vw;
  color: white;
  z-index: 99;
  position: absolute;
  width: 100%;
  margin: 0px auto;
  padding: 120px 0px;
  
  display: grid;
  grid-template-columns: repeat(13, 1fr);
}

h1, h2, h3, blockquote {
  font-family: elevon, sans-serif;
  font-weight: 700;
  font-style: normal;
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
}



  header {
    /*background: var(--white-bg);*/
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    grid-column: 5 / span 5;
    font-size: 5rem;
    padding: 2rem;
    margin-bottom: var(--spacing);
  }

  section {
    grid-column: 2 / 8;
    padding: 1rem;
    background: var(--dark-bg);
    font-size: 1.25rem;
    line-height: 1.5;
    margin-bottom: var(--spacing);
  }

  blockquote {
    margin: 0;
    padding: 0;
    grid-column: 2 / span 9;
    margin-bottom: var(--spacing);
  }

  blockquote p {
    color: black;
    background-color: white;
    font-size: 4rem;
    display: inline;
    line-height: 1;
  }

  .left {
    grid-column: 6 / 12;
  }


  .floating-parent{
    position:fixed;
    /* top: 7%;
    right: 100px; */
    text-align: center;
    color:white;
  }

  .floating-parent .right-button{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: inline-block;
    background-repeat: no-repeat;
    background-color: transparent;
    background-size: 10px;
  }

  .right-button button{
    background-color: transparent;
    color: white;
     border: none;
     cursor: pointer;
     font-size: 70px;

  }

  .right-button a{
    display:block;
    color:white;
    text-decoration: none;
    padding: 10px 15px;
  }

  .right-button .content{
    display:none;
    background-color: transparent;
    position:relative;
    min-width: 100px;
    text-align: left;
    left:0;

  }

  .right-button:hover .content{
    display:block;

  }

  .right-button:hover button{
    background-color: rgb(228, 227, 227);
    color:rgb(0, 0, 0);
  }
  
  .right-button a:hover{
    background-color: white;
    color:black;

  }